import React from 'react';
import { Button, Icon } from 'antd';

interface Props {
  before?: string;
  after?: string;
  getPrevPage: () => void;
  getNextPage: () => void;
}
const PagingButtons = (props: Props) => {
  return (
    <div className='pagination' style={{display: 'flex', justifyContent: 'flex-end'}}>
      {props.before && (
        <Button onClick={props.getPrevPage}><Icon type='left' /> Prev</Button>
      )}
      {props.after && (
        <Button onClick={props.getNextPage}>Next <Icon type='right' /></Button>
      )}
    </div>
  );
};

export {
  PagingButtons,
};
